import '../i18n/i18n';

import React from "react"
import Navigation from "../components/layout/Navigation";
import {SEO} from "../components/seo";
import {graphql} from "gatsby";

const Privacy = props => {
    const {data} = props;
    const content = data.wpPage.content;

    require("../scss/simple_page.scss");

    return (
        <main className="main-wrapper">
            <Navigation pageNr={'L3GAL'}/>
            <section className="relative min-h-screen justify-center pt-32 pb-36 w-2/3 mx-auto css--simple-styling">
                <div dangerouslySetInnerHTML={{__html: content}}></div>
            </section>
        </main>
    )
}
export default Privacy;
export const Head = () => <SEO title={"Privacy policy"} description={'For business partners, customers and their employees'} keywords={'software development, Iot, web development'}/>

export const pageQuery = graphql`
  query {
      wpPage(slug: {eq: "privacy-policy"}) {
        content
      }
  }
`